import { Component, Input, OnInit } from '@angular/core';
import { HouseModel, TenantsModel } from '../../models/house';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { TenantDialogComponent } from '../dialogs/tenant-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { DatabaseService } from 'app/shared/services/database.service';
import { UIElementsService } from 'app/shared/services/uielements.service';
@Component({
  selector: 'app-house',
  templateUrl: './house.component.html',
  styleUrls: ['./house.component.scss']
})
export class HouseComponent {
  @Input() house: HouseModel;
  
  taskDrop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.house.tenants, event.previousIndex, event.currentIndex);
   
  }

  openDialog(tenant?: TenantsModel, idx?: number): void {
    const newTenant = { label: 'purple' };
    const dialogRef = this.dialog.open(TenantDialogComponent, {
      width: '500px',
      autoFocus: false, 
      maxHeight: '90vh',
      data: tenant
        ? { tenant: { ...tenant }, isNew: false, id: this.house.id, idx }
        : { tenant: newTenant, isNew: true }
    });

    dialogRef.afterClosed().subscribe(async (result) => {
      if (result) {
        const house_copy: HouseModel = {...this.house};
        if (result.isNew) {
          const tenant = 
          new TenantsModel(result.tenant.name, 
            result.tenant.fee, 
            result.tenant.label, 
            result.tenant.phone_number)
          house_copy.tenants.push(result.tenant);
        } else {
          house_copy.tenants.splice(result.idx, 1, result.tenant);
        }
     try {
      await this.databaseService.createWithIdorUpdate('houses' , house_copy, this.house.id);
      this.uiElementsService.addSnackBar('Tenant added!');
     } catch (error) {
      this.uiElementsService.addSnackBar(error);
     }
       
      
      }
    });
  }

async handleDelete() {
  try {
    await this.databaseService.delete('houses',this.house.id);
    this.uiElementsService.addSnackBar('Residence deleted');    
    console.log(this.house.id);
  } catch (error) {
    this.uiElementsService.addSnackBar(error);
  }
   await this.databaseService.delete('houses',this.house.id);
  }

  constructor(private databaseService: DatabaseService, 
    private dialog: MatDialog,
    private uiElementsService: UIElementsService
    ) {}

}
