import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { MatDialog } from '@angular/material/dialog';
import { DatabaseService } from 'app/shared/services/database.service';
import { UIElementsService } from 'app/shared/services/uielements.service';
import { Subscription } from 'rxjs';
import { HouseDialogComponent } from '../dialogs/house-dialog.component';
import { HouseModel, TenantsModel } from '../../models/house';
import { Stats } from '../../models/stats';
import { HouseService } from '../../services/house.service';

@Component({
  selector: 'app-kanban',
  templateUrl: './kanban.component.html',
  styleUrls: ['./kanban.component.scss']
})
export class KanbanComponent implements OnInit {
  houses: HouseModel[] = [];
  sub: Subscription = new Subscription();
  constructor(private dialog: MatDialog,
    private databaseService: DatabaseService,
    private afAuth: AngularFireAuth,
    private houseService: HouseService,
    private uiElementsService: UIElementsService) { }

async ngOnInit(){
  this.uiElementsService.addSpinner();
  try {
    const user = await this.afAuth.currentUser;    
     this.databaseService.readMultiple('houses', 'userId', user.uid).subscribe((houses) => {
      this.uiElementsService.removeSpinner(); 
      this.houses = houses;
       let tenants: number = 0;
       
       houses.forEach((value: HouseModel) => {
         tenants += value.tenants.length;
       });
       const stats = new Stats(tenants, 0, houses.length, 0);
       this.houseService.statsSubject.next(stats);
       console.log(houses);
     })
  } catch (error) {
    this.uiElementsService.addSnackBar(error);
  }


  }
  drop(event: CdkDragDrop<string[]>){
    moveItemInArray(this.houses, event.previousIndex, event.currentIndex); 
  }
  openHouseDialog(){
    const dialogRef = this.dialog.open(HouseDialogComponent, 
      { 
        width: '400px',
        data: { }
      });
      dialogRef.afterClosed().subscribe(async (result) => {
        
        if(result){
          this.uiElementsService.addSpinner();
          try {
            const user = await this.afAuth.currentUser;
            const house = new HouseModel(user.uid, result.name, result.type);
           await this.databaseService
            .createGeneric('houses', house);
            
         
            this.uiElementsService.addSnackBar('House added');
          } catch (error) {
            this.uiElementsService.addSnackBar(error);
          }
          this.uiElementsService.removeSpinner();
        }
      })
  }

}
