export interface Stats {
    tenants? : number;
    revenue?: number;
    residences?: number;
    o_payments?: number;
}

export class Stats{
    tenants?: number;
    revenue?: number;
    residences?: number;
    o_payments?: number;

    constructor(tenants: number =0, revenue: number = 0, residences: number = 0, o_payments: number = 0){
        this.residences = residences;
        this.revenue = revenue;
        this.tenants = tenants;
        this.o_payments = o_payments;
    }
}
