import { Component, OnInit, OnDestroy } from '@angular/core';
import { DatabaseService } from 'app/shared/services/database.service';
import { Subscription } from 'rxjs/Subscription';
import { Stats } from '../../models/stats';
import { HouseService } from '../../services/house.service';

@Component({
  selector: 'app-stats',
  templateUrl: './stats.component.html',
  styleUrls: ['./stats.component.css']
})
export class StatsComponent implements OnInit, OnDestroy {
  stats: Stats = new Stats();
  statsArray: {title?: string; icon?: string; stat?: number, class?: string}[] = this.getItemsList(this.stats);
  subscript: Subscription = new Subscription();
  constructor(private databaseService: DatabaseService,
    private houseService: HouseService) { }

  ngOnInit(): void {
     this.subscript = this.houseService.statsSubject.subscribe((value) => {
       this.stats = value;
       this.statsArray = this.getItemsList(value);
     })
  }
  ngOnDestroy(){
    this.subscript.unsubscribe();
  }
  getItemsList(stats: Stats){
    return [
      { 
        title: 'No. of tenants',
        icon: 'person_pin',
        stat: stats.tenants,
        class: 'card-header-warning'
      }, 
      { 
        title: 'Revenue',
        icon: 'date_range',
        stat: stats.revenue,
        class: 'card-header-success'
      },
      { 
        title: 'Residences',
        icon: 'house',
        stat: stats.residences,
        class: 'card-header-info'
      },
      { 
        title: 'Outstanding payments',
        icon: 'money_off',
        stat: stats.o_payments,
        class: 'card-header-danger'
      }]
  }
}
