import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-house-dialog',
  template: `
       <h1 mat-dialog-title>Residence</h1>
    <div mat-dialog-content>
      <form #form="ngForm">
      <p>Name of residence</p>
      <mat-form-field>
        <input placeholder="Name" matInput 
        name="name"
        required
        ngDefaultControl
        [(ngModel)]="data.name" />
      </mat-form-field>
    
      <div mat-dialog-content>
    <p>Type of residence</p>
    <mat-form-field appearance="fill">
       <!-- <mat-label>Favorite food</mat-label> -->
        <mat-select [(ngModel)]="data.type"
          ngDefaultControl
          required
          name="select">
          <mat-option *ngFor="let type of typeOptions" 
          
          [value]="type">
            {{type}}
          </mat-option>
        </mat-select>
    </mat-form-field>
    </div>
    <div mat-dialog-actions>
      <button mat-button (click)="onNoClick()">Cancel</button>
      <button 
      [disabled]="form.invalid"
      mat-button [mat-dialog-close]="data" cdkFocusInitial>
        Add
      </button>
    </div>  
    </form>
    
    </div>
    
  `,
  styles: [
  ]
})
export class HouseDialogComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<HouseDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }
    typeOptions = ['Commune', 'Flat', 'Town house']
  ngOnInit(): void {
  }
  onNoClick(){
    this.dialogRef.close();
  }
}
