export interface HouseModel {
    id?: string;
    userId?: string;
    name?: string;
    tenants?: TenantsModel[];
    type?: string | 'Commune' | 'Flats' | 'Complex/Estate' | 'Town House';
    capacity?: number;
    }
    
    export class HouseModel{
      id?: string;
      userId?: string;
      name?: string;
      tenants?: TenantsModel[];
      type?: string | 'Commune' | 'Flats' | 'Complex/Estate' | 'Town House';
      capacity?: number;
    
      constructor(id: string = '', 
      name: string = '',
      type: string = '',
      tenants: TenantsModel[] = [],
      
      capacity: number = 0){
        this.userId = id;
        this.name = name;
        this.tenants = tenants;
        this.type = type;
        this.capacity = capacity;
      }
    }
    
    export interface TenantsModel {
      id?: any;
      name?: string;
      house_id?: any;
      fee?: number;
      label?: string;
      phone_number?: string;
    //   rentPogress?: RentModel[];
     rentPogress?: {
         'jan': number,
         'feb': number,
         'mar': number,
         'apr': number,
         'may': number,
         'jun': number,
         'jul': number,
         'aug': number,
         'sept': number,
         'oct': number,
         'nov': number,
         'dec': number


     }
    }
    export class TenantsModel {
        name?: string;
        fee?: number;
        label?: string;
        phone_number?: string;
      //   rentPogress?: RentModel[];
       rentPogress?: {
           'jan': number,
           'feb': number,
           'mar': number,
           'apr': number,
           'may': number,
           'jun': number,
           'jul': number,
           'aug': number,
           'sept': number,
           'oct': number,
           'nov': number,
           'dec': number
       }
       constructor(name: string = '', 
       fee: number = 0, 
       label: string = '', 
       phone_number: string = '', 
       rentPogress: any = {}){
        this.name = name;
        this.fee = fee;
        this.label = label;
        this.phone_number = phone_number;
        this.rentPogress = rentPogress;
       }
    }
    export interface RentModel{
      year: number;
      months_paid: MonthsRentModel[]
    }
    export interface MonthsRentModel {
      month: boolean;
      amount: number;
    }
    