import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { HouseService } from 'app/profile/services/house.service';
import {DatabaseService} from 'app/shared/services/database.service'
@Component({
  selector: 'app-tenant-dialog',
  template: `
    
  <h1 mat-dialog-title>Tenants</h1>
  <mat-dialog-content>
    <form #form="ngForm">
    <p>Name of tenant</p>
    <mat-form-field>
      <input placeholder="Name" matInput 
      name="name"
      required
      ngDefaultControl
      [(ngModel)]="data.tenant.name" />
    </mat-form-field>
  
  
  <p>Rent amount</p>
  <mat-form-field appearance="fill">
     <!-- <mat-label>Favorite food</mat-label> -->
     <input placeholder="amount" matInput 
      name="fee"
      type="number"
      required
      ngDefaultControl
      [(ngModel)]="data.tenant.fee" />
  </mat-form-field>
  <p>Phone number (optional)</p>
  <mat-form-field appearance="fill">
     <!-- <mat-label>Favorite food</mat-label> -->
     <input placeholder="Phone number" matInput 
      name="phone"
      type="number"
      ngDefaultControl
      [(ngModel)]="data.tenant.phone_number" />
  </mat-form-field>
    <br />
    <mat-button-toggle-group
      #group="matButtonToggleGroup"
      [(ngModel)]="data.tenant.label"
      name="label"
    >
      <mat-button-toggle *ngFor="let opt of labelOptions" [value]="opt">
        <mat-icon [ngClass]="opt">{{
          opt === 'gray' ? 'check_circle' : 'lens'
        }}</mat-icon>
      </mat-button-toggle>
    </mat-button-toggle-group>
  <div mat-dialog-actions>
    <button 
    [disabled]="form.invalid"
    mat-button [mat-dialog-close]="data" cdkFocusInitial>
      {{ data.isNew ? 'Add Tenant' : 'Update tenant' }}
    </button>

    <app-delete
      (delete)="handleTaskDelete()"
      *ngIf="!data.isNew"
    ></app-delete>
    <button mat-button (click)="dialogRef.close()" cdkFocusInitial>
      close
    </button>
  </div>
  </form>
  </mat-dialog-content>
  `,
  styleUrls: ['./dialogs.scss']
})
export class TenantDialogComponent {
  labelOptions = ['purple', 'blue', 'green', 'yellow', 'red', 'gray'];

  constructor(
    public dialogRef: MatDialogRef<TenantDialogComponent>,
    private databaseService: DatabaseService,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

  handleTaskDelete() {
    this.databaseService.removeFromArray('houses' ,this.data.id, this.data.tenant);
    this.dialogRef.close();
  }
}
